.intro {
    display: flex;
    height: 100vh;
}

/* left side */

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1, h2 {
    margin: 0;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-greeting {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 15s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #519259;
    display: flex;
    align-items: center;
}

/* right side */

.i-right {
    flex: 1;
    position: relative;
}

.i-bg {
    clip-path: polygon(100% 0%, 81% 49%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #519259;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.i-img {
    width: 100%;
    height: 100%;
    position: absolute;
}

/* responsive */

@media screen and (max-width: 1220px) {
    .i-left-wrapper {
        justify-content: center;
    }

    .i-greeting {
        font-size: 30px;
    }

    .i-name {
        font-size: 45px;
    }
}

@media screen and (max-width: 960px) {
    .i-left-wrapper {
        width: 100%;
    }

    .i-greeting {
        font-size: 22px;
    }

    .i-name {
        font-size: 40px;
    }

    .i-title-item {
        font-size: 27px;
    }

    .i-desc {
        font-size: 16px;
    }
}

@media screen and (max-width: 870px) {
    .i-greeting {
        font-size: 20px;
    }

    .i-name {
        font-size: 35px;
    }

    .i-desc {
        font-size: 15px;
    }
}

@media screen and (max-width: 785px) {
    .intro {
        flex-direction: column;
        max-width: 100vh;
    }

    .i-left-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }
  
    .i-greeting {
        font-size: 24px;
    }

    .i-name {
        font-size: 40px;
        margin-bottom: 10px;
    }

    .i-desc{
    display: none;
    }

    .i-right {
        width: 85%;
        margin: 0 auto;
    }
}


@media screen and (max-width: 480px) {
    .intro {
      flex-direction: column;
      max-width: 100vh;
    }
  
    .i-left-wrapper {
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }

    .i-greeting {
        font-size: 20px;
    }

    .i-name {
        font-size: 32px;
        margin-bottom: 10px;
    }

    .i-title-item {
        font-size: 24px;
    }
  
    .i-desc{
      display: none;
    }

    .i-right {
        width: 85%;
        margin: 0 auto;
    }
}